<template>
    <div class="surveyArchives">
        
        <!-- Header -->
        <el-row class="Header">
            <el-col :span="24">
                <div class="grid-content bg-purple-dark">
                    <div class="select-item">
                        <!-- 线路 -->
                        <p>路线：</p>
                        <el-dropdown class="select_" @command="changeLine">
                        <span class="el-dropdown-link">
                            {{save.line.routename}}<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item v-for="item in line" :key="item.id" :command="item">{{item.routename}}</el-dropdown-item>
                        </el-dropdown-menu>
                        </el-dropdown>
                        <!-- 站点 -->
                        <p style="margin-left: 20px;">站点：</p>
                        <el-dropdown class="select_"  @command="changeSite">
                        <span class="el-dropdown-link">
                            {{save.site.sitename}}<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item v-for="item in site" :key="item.id" :command="item">{{item.sitename}}</el-dropdown-item>
                        </el-dropdown-menu>
                        </el-dropdown>
                        <!-- 项目名称 -->
                        <p style="margin-left: 20px;">项目名称：</p>
                        <el-dropdown class="select_"  @command="changeSite">
                        <span class="el-dropdown-link">
                            {{save.site.sitename}}<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item v-for="item in site" :key="item.id" :command="item">{{item.sitename}}</el-dropdown-item>
                        </el-dropdown-menu>
                        </el-dropdown>
                    </div>
                </div>
            </el-col>
        </el-row>
        <!-- Header END -->


        <div class="tableCon">
            <template>
                <el-table
                    :data="tableData"
                    style="width: 100%; border: 1px solid #eee;border-bottom: none;">
                    <el-table-column
                    label="项目名称"
                    width="280">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.name }}</span>
                    </template>
                    </el-table-column>
                    <el-table-column
                        label="检测时间"
                        width="280">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.date }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="综合预警"
                        width="280">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.date }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="提交人"
                        width="280">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.date }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="联系电话"
                        width="280">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.date }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                        <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
                    </el-table-column>
                </el-table>
            </template>
        </div>




    </div>
</template>
<script>
export default {
    data () {
      return {
        page: '0',
        line: [],
        site: [],
        save: {
          line: {},
          site: {},
          projectMsg: {},
          weather: {}
        },
        tableData: [],
        siteID: ''
      }
    },
    methods: {
        // 改变线路
        changeLine (choose) {
            console.log(choose)
            var _this = this;
            this.save.line = choose;
            api.getLinesite(choose.id).then(res => {
            console.log(res.data.list)
            this.site = res.data.list;
            this.save.site = res.data.list[0];
            this.siteID = res.data.list[0].id;
            })
            this.getMainpageinformactionSelectbyrid(this.save.site.id);
        },
        // 获取线路
        getSelectAll() {
            api.getSelectAll().then(res => {
            console.log(res.data.list)
            this.line = res.data.list;
            this.save.line = res.data.list[0]
            })
        },
        // 改变站点
        changeSite(choose) {
            console.log(choose);
            this.save.site = choose;
            this.siteID = choose.id;
            this.getMainpageinformactionSelectbyrid(this.save.site.id);
        },
        // 获取站点
        getLinesite(routeid) {
            api.getLinesite(routeid).then(res => {
            console.log('****')
            console.log(res)
            console.log(res.data.list)
            this.site = res.data.list;
            this.save.site = res.data.list[0];
            this.siteID = res.data.list[0].id;
            this.getMainpageinformactionSelectbyrid(this.save.site.id)
            })
        },
        // 检测项目 列表
        getMeasurementproject(id, pageNum) {
            api.getMeasurementproject(id, pageNum, 10).then(res => {
            console.log(res.data.list)
            this.tableData = res.data.list;
            })
        },
        // 获取首页项目信息
        getMainpageinformactionSelectbyrid(rid) {
            api.getMainpageinformactionSelectbyrid(rid).then(res => {
            console.log('*********************')
            console.log(res.data.msg)
            this.save.projectMsg = res.data.msg;
            this.getWeather(this.save.projectMsg.weatherid);
            })
        },
    },
}
</script>
<style lang="stylus" scoped>
.Header 
    color #fff
    display flex
    align-items center
    .select-item
        display flex
        align-items center
.top_bt 
    display flex
    align-items center
    color #fff
    p
        margin-left 30px
.tableCon
    margin-top 16px
    padding 20px 
    border-radius 12px
    overflow hidden
    background #fff


</style>



